@import '../../css/stylesheet.scss';

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  font-family: bahnschrift;
}

html {
  scroll-behavior: smooth;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

ul {
  list-style: none;
}

@mixin mobile {
  @media screen and (max-width: 767px) {
    @content;
  }
}

@mixin pc {
  @media screen and (min-width: 768px) {
    @content;
  }
}

html [data-lazy] {
  background-image: none !important;
}
#footer {
  width: 100%;
  height: 400px;
  background: #101313;
  overflow: hidden;

  @include mobile {
    height: 288px;
  }

  .logo {
    width: 293px;
    height: 48px;
    float: left;
    margin: 60px 391px 0 100px;

    @include mobile {
      width: 146.5px;
      height: 24px;
      margin: 32px 0 0 20px;
      float: unset;
    }
  }

  .text {
    margin-top: 72px;
    float: left;

    @include mobile {
      margin-top: 0;
      float: unset;
    }

    ul {
      color: #FFF;
      overflow: hidden;

      li {
        font-size: 24px;
        line-height: 26px;
        float: left;

        @include mobile {
          width: 100%;
          height: 40px;
          line-height: 40px;
          padding-left: 20px;
          float: unset;
          font-size: 12px;
        }

        &:not(:last-child) {
          margin-right: 72px;
        }
      }
    }
  }

  .p1 {
    width: 100%;
    margin: 80px 0 0 100px;
    font-size: 20px;
    color: rgba(255, 255, 255, 0.8);
    line-height: 22px;
    float: left;

    @include mobile {
      width: 280px;
      margin: 12px 0 0 20px;
      line-height: 12px;
      float: unset;
      font-size: 12px;
    }
  }

  .p2 {
    width: 100%;
    margin: 10px 0 0 100px;
    font-size: 16px;
    color: rgba(255, 255, 255, 0.8);
    line-height: 18px;
    float: left;

    @include mobile {
      width: 280px;
      margin: 8px 0 0 20px;
      line-height: 12px;
      float: unset;
      font-size: 12px;
    }
  }

  .p3 {
    width: 100%;
    margin: 20px 0 0 100px;
    font-size: 16px;
    color: rgba(255, 255, 255, 0.8);
    line-height: 18px;
    float: left;

    @include mobile {
      width: 280px;
      margin: 8px 0 0 20px;
      line-height: 12px;
      float: unset;
      font-size: 12px;
    }
  }
}