@import '../../css/stylesheet.scss';

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  font-family: bahnschrift;
}

html {
  scroll-behavior: smooth;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

ul {
  list-style: none;
}

@mixin mobile {
  @media screen and (max-width: 767px) {
    @content;
  }
}

@mixin pc {
  @media screen and (min-width: 768px) {
    @content;
  }
}

html [data-lazy] {
  background-image: none !important;
}
#cover {
  @include mobile {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(135deg, #184352 0%, #101313 100%);
    background-size: cover;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 98;
    overflow: hidden;

    ul {
      margin-top: 100px;

      li {
        width: 100%;
        height: 40px;
        color: #FFF;
        font-size: 14px;
        line-height: 40px;
        margin-bottom: 20px;
        text-align: center;
      }
    }

    .request-info-btn {
      width: 140px;
      height: 32px;
      margin: 40px auto 0;
      line-height: 30px;
      background: rgba(255, 255, 255, 0.2);
      border-radius: 20px;
      border: 1px solid rgba(255, 255, 255, 0.8);
      backdrop-filter: blur(2px);
      font-size: 14px;
      color: #FFF;
      text-align: center;
    }
  }
}