@import '../../css/stylesheet.scss';

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  font-family: bahnschrift;
}

html {
  scroll-behavior: smooth;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

ul {
  list-style: none;
}

@mixin mobile {
  @media screen and (max-width: 767px) {
    @content;
  }
}

@mixin pc {
  @media screen and (min-width: 768px) {
    @content;
  }
}

html [data-lazy] {
  background-image: none !important;
}
#navigation {
  width: 100%;
  height: 100px;
  position: absolute;
  background: transparent;
  z-index: 99;

  @include mobile {
    height: 50px;
  }

  .logo {
    width: 293px;
    height: 48px;
    float: left;
    margin: 30px 0 0 100px;

    @include mobile {
      width: 146.5px;
      height: 24px;
      margin: 12px 0 0 20px;
    }
  }

  .text {
    text-align: center;
    color: #FFF;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);

    @include mobile {
      width: 20px;
      height: 50px;
      line-height: 50px;
      position: static;
      float: right;
      margin-right: 20px;
      transform: translate3d(0, 0, 0);
    }

    #open {
      display: none;

      @include mobile {
        display: inherit;
      }
    }

    ul {
      overflow: hidden;

      @include mobile {
        display: none;
      }

      li {
        font-size: 24px;
        line-height: 26px;
        float: left;

        &:not(:last-child) {
          margin-right: 154px;
        }
      }
    }
  }

  .request-info-btn {
    width: 192px;
    height: 40px;
    margin: 30px 100px 0 0;
    line-height: 38px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 20px;
    border: 1px solid rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(2px);
    font-size: 18px;
    color: #FFF;
    text-align: center;
    float: right;

    @include mobile {
      display: none;
    }
  }
}